h2, h3 {
  text-align: center;
}
h3 {
  margin-top: 25px !important;
}
.slideshow {
  text-align: center;
  background: white;
  max-width: 50%;
  margin: 0 auto;
  margin-top: 3rem;
  padding: 20px;
  border-radius: 40px;
  max-height: 80vh;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
}

.slideshow img {
  max-width: 100%;
}

.slideshow div.placeholder {
  position: relative;
}

.slideshow div.mask {
  position: absolute;
  z-index: 99;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.nav {
  padding: 0 10px;
  cursor: pointer;
}

.pay-now {
  margin: 10px auto;
}

*.no-select {
   -moz-user-select: none;
   -khtml-user-select: none;
   -webkit-user-select: none;
   -ms-user-select: none;
   user-select: none;
}
